import React from 'react';
import { useNavigate } from 'react-router-dom';
import Button from './Button'; // ou qualquer biblioteca de estilo que voc estiver usando
// import styled from 'styled-components';


const LogoutButton = () => {
  const navigate = useNavigate();

  const handleLogout = () => {
    // Aqui voc pode adicionar a lgica de logout, como limpar o token de autenticao
    localStorage.removeItem('authToken');  // Exemplo de remoo de token do localStorage
    sessionStorage.removeItem('authToken'); // Se estiver usando sessionStorage, pode remover tambm

    // Redireciona para a pgina de login ou qualquer outra pgina
    navigate('/');
  };

  return (
    <Button onClick={handleLogout}>Sair</Button>
  );
};

export default LogoutButton;